.categories{
  padding:30px;
  display:flex;
  align-items:center;
  justify-content:space-between;
  flex-wrap: wrap;
  gap:20px;
}
.category-item{
  width: 15%;
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: var(--white-color);
  border-radius: 10px;
  padding: 5px;
}
.category-item-img{
  width: 100%;
  height: 100px;
  object-fit: contain;
  margin-bottom: 10px;
}
.category-item-title{
  font-size: 20px;
  color: var(--primay-color);
  padding: 0 10px;
  text-align: center;
}

@media (max-width:1200px) {
  .category-item{
    width: 20%;
  }
  
}
@media (max-width:920px) {
  .categories{
    padding: 15px;
  }
  .category-item{
    width: 45%;
  }
  
}
@media (max-width:520px) {
  .categories{
    justify-content: center;
  }
  .category-item{
    width: 80%;
  }
  
}